body {
    background-color: blue;
    padding: 20px ;
    margin: 0 ; 
}

h1,h2,p,ul,li {
    font-family: sans-serif;
}
h1.titulo {
    text-align: center;
    font-weight: bold;

}

ul.header li {
    display: inline;
    list-style-type: none;
    margin: 0; 

}
ul.header {
    background-color: #111;
    padding: 0px ;

}
ul.header li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
  }
.content {
    background-color: #FFF ;
    padding: 20px ; 
}

.content h2 {
    padding: 0;
    margin: 0;
}
.content li {
    margin-bottom: 10px;
}
.head {
    -webkit-margin-after: 1em;
            margin-block-end: 1em;
    -webkit-margin-before: 1em ;
            margin-block-start: 1em ; 
}
.head button {
    color: white;
    background-color: #111;
    -webkit-margin-start: 1em ;
            margin-inline-start: 1em ;
    border: none;
    padding: 5px 32px;
    text-align: center; 
    display: inline-block;
    text-decoration: none;
    border-radius: 4px;  
}
.head button:hover {
    background-color: #3e8e41 ;
}
.ReactModal__Body--open {
    overflow: hidden;
}

@media(max-width:576px){
    .hola {
        font-size: 10px;
    }
}
.Card_CV {
    border: 0.3rem solid red;
    text-align: center; 
}


